.related-products{margin-top:40px;padding:0 15px}.related-products h3{font-size:24px;font-weight:600;margin-bottom:25px;position:relative;padding-bottom:10px}.related-products h3:after{content:"";position:absolute;bottom:0;left:0;width:60px;height:3px;background-color:#007edb}.related-products .loading,.related-products .error{text-align:center;padding:20px;color:#666;background-color:#f5f5f7;border-radius:6px}.related-products .error{color:#e53935;background-color:#ffebee}.related-products .related-products-grid{display:grid;grid-template-columns:repeat(auto-fill, minmax(220px, 1fr));gap:25px}@media(max-width: 768px){.related-products .related-products-grid{grid-template-columns:repeat(auto-fill, minmax(160px, 1fr));gap:15px}}.related-products .related-product-item{text-decoration:none;color:#333;text-align:center;border-radius:8px;padding:15px;transition:all .3s ease;display:flex;flex-direction:column}.related-products .related-product-item:hover{box-shadow:0 5px 15px rgba(0,0,0,.1);transform:translateY(-3px)}.related-products .related-product-item:hover h4{color:#007edb}.related-products .related-product-item img{width:100%;height:180px;object-fit:contain;margin-bottom:15px;transition:transform .3s ease}@media(max-width: 768px){.related-products .related-product-item img{height:140px}}.related-products .related-product-item h4{font-size:16px;font-weight:500;margin-bottom:10px;color:#333;transition:color .3s ease;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;height:44px}.related-products .related-product-item .price{font-weight:600;font-size:18px;color:#007edb;margin-top:auto}.related-products .related-product-item .price::after{content:"đ";font-weight:400}