.footer{background-color:#2a2d30;color:#fff;padding:30px 10%;font-family:"Segoe UI",Tahoma,Geneva,Verdana,sans-serif;margin-top:5%;box-shadow:0 -5px 15px rgba(0,0,0,.1)}.footer .container{display:flex;justify-content:space-between;flex-wrap:wrap;gap:20px}.footer .column{flex:1;width:calc(33.333% - 20px);min-width:0;max-width:none;padding:0 20px;transition:transform .3s ease}.footer .column:hover{transform:translateY(-5px)}.footer .column h3{font-size:1.25rem;margin-bottom:20px;color:#fff;position:relative;padding-bottom:10px;font-weight:600}.footer .column h3:after{content:"";position:absolute;bottom:0;left:0;width:50px;height:3px;background-color:gold;border-radius:2px}.footer .column .address{color:#e0e0e0;text-decoration:none;font-weight:500;transition:color .3s ease,transform .3s ease;display:inline-block}.footer .column .address:hover{color:gold;transform:translateX(5px)}.footer .column p{font-size:1rem;margin-bottom:15px;color:#e0e0e0;line-height:1.6}.footer .column ul{list-style:none;padding:0}.footer .column ul li{margin:12px 0;transition:all .3s ease;position:relative;padding-left:15px}.footer .column ul li:before{content:"›";position:absolute;left:0;color:gold;font-weight:bold;opacity:0;transition:opacity .3s ease,transform .3s ease}.footer .column ul li a{text-decoration:none;color:#e0e0e0;transition:color .3s ease}.footer .column ul li:hover{padding-left:20px}.footer .column ul li:hover:before{opacity:1;transform:translateX(3px)}.footer .column ul li:hover a{color:gold}.footer .column form{display:flex;flex-direction:column;margin-top:15px}.footer .column form input{margin-bottom:12px;padding:12px 15px;border:none;border-radius:5px;background-color:hsla(0,0%,100%,.1);color:#fff;transition:background-color .3s ease,transform .3s ease}.footer .column form input:focus{background-color:hsla(0,0%,100%,.2);outline:none;transform:translateX(3px)}.footer .column form input::placeholder{color:#bbb}.footer .column form button{padding:12px 15px;background-color:#a1541b;color:#fff;border:none;border-radius:5px;cursor:pointer;font-weight:600;letter-spacing:.5px;transition:all .3s ease;position:relative;overflow:hidden;z-index:1}.footer .column form button:before{content:"";position:absolute;top:0;left:-100%;width:100%;height:100%;background:linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);transition:all .6s ease;z-index:-1}.footer .column form button:hover{background-color:#833f13;transform:translateY(-3px);box-shadow:0 5px 15px rgba(0,0,0,.3)}.footer .column form button:hover:before{left:100%}.footer .column form button:active{transform:translateY(0);box-shadow:0 2px 5px rgba(0,0,0,.3)}.footer .logo{max-width:150px;margin-bottom:15px;transition:transform .3s ease}.footer .logo:hover{transform:scale(1.05)}.footer .socialIcons{display:flex;gap:15px;margin-top:20px}.footer .socialIcons .icons{font-size:1.8rem;cursor:pointer;color:#e0e0e0;transition:all .3s ease;padding:8px;border-radius:50%;background-color:hsla(0,0%,100%,.1);display:flex;align-items:center;justify-content:center}.footer .socialIcons .icons:hover{color:gold;transform:translateY(-5px) rotate(5deg);background-color:hsla(0,0%,100%,.2);box-shadow:0 5px 15px rgba(0,0,0,.3)}.crack-column-tong{display:flex;height:450px;align-items:center;width:30px;justify-content:center;flex-shrink:0}.crack-column{position:relative;width:1px;height:80%;background:linear-gradient(to bottom, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.05));border-radius:1px;overflow:hidden}.crack-column:after{content:"";position:absolute;top:-50%;left:0;width:100%;height:50%;background:hsla(0,0%,100%,.7);animation:light-flow 3s infinite linear}@keyframes light-flow{0%{top:-50%}100%{top:100%}}@media(max-width: 992px){.footer .container{flex-wrap:wrap;gap:30px}.footer .column{width:calc(50% - 30px);padding:0 15px}.crack-column-tong{display:none}}@media(max-width: 768px){.footer{padding:40px 5%}.footer .container{flex-direction:column;gap:40px}.footer .column{width:100%;border-bottom:1px solid hsla(0,0%,100%,.1);padding-bottom:30px;padding:0 0 30px 0}.footer .column:last-child{border-bottom:none}.footer .crack-column-tong{display:none}}